.topnav1 {
  overflow: hidden;
  display: flex;
  justify-content: center;
  justify-content: space-around;
  align-items: center;
  background-color: #696969;
  margin-left: 30%;
  margin-right: 30%;
  margin-top: 2%;
  margin-bottom: 2%;
  border: solid 2px white;
  border-radius: 15px;
  height: 55px;
}

.topnav1 li.active1 {
  float: left;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  list-style-type: none;
  transition: 2s;
}

a {
  color: black;
  font-size: 23px;
  transition: 2s;
  text-decoration: none;
}
a:hover {
  color: yellow;
}
